.chartContainer {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  height: 280px;
  justify-content: center;
  width: 280px;
  cursor: pointer;
  position: relative;
  transition: scale 250ms ease-in-out;
  z-index: 3;
}

.chartContainer:hover {
  scale: 1.05;
}

.chartCont {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 4px;
  justify-content: center;
  position: absolute;
}

.chartBalance {
  color: var(--white, #fbfbfb);
  cursor: pointer;
  display: flex;
  gap: 5px;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  margin: 0 auto;
  text-align: center;
}

.light {
  color: var(--welcome-bg-gradient);
}

.violet {
  color: #5255bc;
  filter: brightness(1.5);
}

.chart {
  cursor: pointer;
  height: 100%;
  width: 100%;
}

.tooltipContainer {
  background-color: black;
  border-radius: 4px;
  cursor: pointer;
  color: var(--white);
  display: flex;
  flex-direction: column;
  font-size: 12px;
  gap: 2px;
  padding: 4px 6px;
  position: fixed;
  height: fit-content;
  width: fit-content;
  z-index: 10;
}

.violet {
  color: #5255bc;
  filter: brightness(1.5);
}

.tooltipNameContainer {
  padding: 0;
  margin: 0;
}

.tooltipSumContainer {
  display: flex;
  justify-content: space-between;
  gap: 5px;
  height: fit-content;
  width: fit-content;
  padding: 0;
  margin: 0;
}

.tooltipSumContainer div {
  height: 14px;
  width: 14px;
  background-color: #fbfbfb;
}
